.intro {
	display: flex;
	height: 100vh;

	@media screen and (max-width: 500px) {
		flex-direction: column;
		justify-content: center;
		align-items: center;
		height: 100vh;
	}

	.intro-left {
		flex: 1;
		display: flex;
		align-items: center;
		justify-content: center;
		flex-direction: column;
	}

	.intro-left-wrapper {
		padding: 200px;
		display: flex;
		flex-direction: column;
		justify-content: space-between;

		@media screen and (max-width: 500px) {
			padding: 10px;
			align-items: center;
			text-align: center;
			justify-content: center;
			margin-top: 150px;
			height: 100%;
		}

		.intro-introduction {
			font-size: 60px;
			font-weight: 300;

			@media screen and (max-width: 500px) {
				font-size: 40px;
			}
		}

		.intro-name {
			font-size: 80px;

			@media screen and (max-width: 500px) {
				font-size: 60px;
			}
		}

		.intro-title {
			height: 55px;
			overflow: hidden;

			.intro-title-wrapper {
				height: 100%;
				animation: move 8s ease-in-out infinite alternate;
				display: flex;
				flex-direction: column;

				@media screen and (max-width: 500px) {
					align-items: center;
				}

				.intro-title-item {
					height: 50px;
					font-size: 40px;
					font-weight: bold;
					color: #4d84e1;

					display: flex;
					align-items: center;
				}
			}
		}

		.intro-description {
			@media screen and (max-width: 500px) {
				display: none;
			}
		}

		.button.scroll {
			padding: 8px;
			background: #212529;
			display: inline-block;
			width: 50px;
			height: 50px;
			text-align: center;
			vertical-align: middle;
			border-radius: 50%;
			position: absolute;
			bottom: 20px;
			left: 100px;

			@media screen and (max-width: 500px) {
				display: none;
			}

			.scroll-down-arrow {
				margin: 18px 0 0;
				position: absolute;
				left: 28px;
				top: 7px;
				-webkit-animation: bounce 1000ms ease infinite;
				-moz-animation: bounce 1000ms ease infinite;
				-o-animation: bounce 1000ms ease infinite;
				animation: bounce 1000ms ease infinite;
				-webkit-animation-timing-function: cubic-bezier(0.14, 0.75, 0.2, 1.01);
				-moz-animation-timing-function: cubic-bezier(0.14, 0.75, 0.2, 1.01);
				-ms-animation-timing-function: cubic-bezier(0.14, 0.75, 0.2, 1.01);
				animation-timing-function: cubic-bezier(0.14, 0.75, 0.2, 1.01);
				border-left: 5px solid transparent;
				border-right: 5px solid transparent;
				border-top: 8px solid #f2f2f2;
				content: '';

				@media screen and (max-width: 500px) {
					position: absolute;
					left: 50vw;
					top: 100px;
				}
			}
			.button.scroll:before {
				position: absolute;
				top: 0;
				left: 0;
				box-shadow: 0px 0px 3px white, inset 0px 0px 3px white;
				padding: 2px;
				z-index: -1;
				width: 32px;
				height: 32px;
				border-radius: 50%;
				display: block;
				content: '';
				-webkit-animation: ring 1s alternate 0s infinite;
				-moz-animation: ring 1s alternate 0s infinite;
				-o-animation: ring 1s linear 0s infinite;
				animation: ring 1s linear 0s infinite;
			}
		}
	}
}

.intro-right {
	flex: 1;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;

	.intro-right-background {
		clip-path: polygon(84% 3%, 0 66%, 100% 100%);
		height: 580px;
		width: 420px;
		background-color: #212529;
		display: flex;
		align-items: center;
		justify-content: center;
		z-index: -1;
		position: absolute;
		right: 150px;

		@media screen and (max-width: 500px) {
			display: none;
		}

		@media screen and (max-width: 1100px) {
			display: none;
			// margin-bottom: 200px;
		}
	}

	.img-wrapper {
		.intro-picture {
			height: 400px;
			width: 400px;
			position: absolute;
			right: 150px;
			bottom: 300px;
			z-index: 3;

			@media screen and (max-width: 1100px) {
				display: none;

				// margin-bottom: 200px;
			}

			@media screen and (max-width: 500px) {
				display: block;

				height: 300px;
				width: 300px;
				left: 10vw;
				bottom: 0px;
				right: 0px;
				top: 30vh;
				// margin-bottom: 200px;

				margin: auto;
			}
		}
	}
}

@keyframes move {
	25% {
		transform: translateY(-50px);
	}
	50% {
		transform: translateY(-100px);
	}
	75% {
		transform: translateY(-150px);
	}
	100% {
		transform: translateY(-200px);
	}
}

@-webkit-keyframes bounce {
	0%,
	100% {
		top: 9px;
		opacity: 0.5;
	}
	25% {
		top: 12px;
		opacity: 1;
	}
	50% {
		opacity: 0.8;
	}
}
@keyframes bounce {
	0%,
	100% {
		top: 9px;
		opacity: 0.5;
	}
	25% {
		top: 12px;
		opacity: 1;
	}
	50% {
		opacity: 0.8;
	}
}
@-webkit-keyframes ring {
	0%,
	100% {
		-webkit-transform: scale(1, 1);
		opacity: 1;
	}
	75% {
		-webkit-transform: scale(1.2, 1.2);
		opacity: 0.4;
	}
}
@keyframes ring {
	0%,
	100% {
		transform: scale(1, 1);
		opacity: 1;
	}
	75% {
		transform: scale(1.2, 1.2);
		opacity: 0.4;
	}
}
